}
.UserDialog > nav:first-child label {
  margin-left: 2em;
  cursor: pointer;
} */

.UserDialog .signInOrSignUp > nav:first-child{ padding: 1em; margin-left: -2em;}
.UserDialog .signInOrSignUp > nav:first-child label{ margin-left: 2em; cursor: pointer;}
.UserDialog .signInOrSignUp > .panes { padding: 0 1em 1em; }

/* .UserDialog > .panes {
  padding: 0 1em 1em;
} */

/* .UserDialog .forgotPassword > h3 {

} */

.UserDialog .forgotPassword > h3 { padding: 1em; }
.UserDialog .forgotPassword > form { padding: 0 1em 1em; }

.UserDialog form > .row {
  display: -webkit-flex;
  display: flex;
  padding: 0.3em 0;
}
.UserDialog form > .row > label:first-child {
  width: 4em;
}
/* .UserDialog form > .row.actions {
  /* padding-left: 4em;
} */ */
.UserDialog form>.row.actions{ padding-left: 4em; display: -webkit-flex; display: flex; -webkit-align-items: center; align-items: center;}
.UserDialog form>.row.actions button{ margin-right: .5em; }

.UserDialog button{font: inherit; padding: .2em;}

