	background: #5599FF;
}
input[type=checkbox],input[type=radio] {
    -webkit-appearance: none;
    -moz-appearance: none;
         appearance: none;
    outline: none;
    width: 20px;
    height: 20px;
    cursor: pointer;
    vertical-align: center;
    background: #fff;
    border: 1px solid #ccc;
    position: relative;
}

input[type=checkbox]:checked::after {
    content: "\2713";
    display: block;
    position: absolute;
    top: -5px;
    left: -1px;
    right: 0;
    bottom: 0;
    /* width: 20px; */
    /* height: 20px; */
    font-size: 35px;
    line-height: 20px;
    /* border: 1px solid #ddd; */
    color: red;
    /* font-size: 12px; */
}
input[type=radio]:checked::after {
    content: "";
    display: block;
    position: absolute;
    top: 2px;
    left: 2px;
    right: 0;
    bottom: 0;
    width: 6px;
    height: 6px;
    background-color: red;
}
input[type=radio], input[type=radio]:checked::after {
    border-radius: 50%;
}
